.promo__container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 120px 45px;
  padding-bottom: 145px;

  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 40px;

  @media (max-width: $tablet-width) {
    padding: 100px 46px;
    padding-bottom: 85px;
    column-gap: 32px;
  }

  @media (max-width: $mobile-width) {
    padding: 65px 15px;
    padding-bottom: 85px;
    grid-template-columns: 1fr;
  }
}

.promo__description {
  @media (max-width: $tablet-width) {
    max-width: 324px;
    transform: translateY(-9px);
  }

  h2 {
    margin: 0;
    margin-bottom: 23px;
    padding: 0;
    padding-top: 56px;

    font-weight: 600;
    font-size: 28px;
    line-height: 42px;

    @media (max-width: $tablet-width) {
      font-size: 26px;
      line-height: 36px;
      padding: 0;
      margin-bottom: 15px;
      max-width: 302px;
    }

    @media (max-width: $mobile-width) {
      font-size: 24px;
      line-height: 34px;
      max-width: 275px;
    }
  }

  p {
    margin: 0;
    padding: 0;
    max-width: 512px;

    @media (max-width: $tablet-width) {
      font-size: 16px;
      line-height: 24px;
    }

    @media (max-width: $mobile-width) {
      max-width: none;
      margin-bottom: 35px;
    }
  }
}

.promo__image {
  @media (max-width: $tablet-width) {
    min-width: 324px;
    min-height: 174px;
  }

  @media (max-width: $mobile-width) {
    min-width: 290px;
    min-height: 156px;
  }
}
