@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Verdana", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.site {
  flex: 1;
}

.header {
  width: 100%;
  min-height: 110px;
  position: absolute;
  z-index: 2;
}

.header__container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 45px;
}
@media (max-width: 767px) {
  .header__container {
    padding: 0 15px;
    padding-right: 7px;
  }
}

.header__nav {
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .header__nav {
    padding-top: 28px;
  }
}
@media (max-width: 767px) {
  .header__nav {
    padding-top: 11px;
  }
}

.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  order: -1;
}

.nav__menu {
  margin: 0;
  padding: 0;
}
@media (max-width: 767px) {
  .nav__menu {
    display: none;
  }
}

.nav__button {
  display: none;
}
@media (max-width: 767px) {
  .nav__button {
    display: block;
    position: relative;
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  .nav__button::after, .nav__button::before {
    content: "";
    position: absolute;
    height: 2px;
    width: 24px;
    background-color: #f9fbfd;
    top: 13px;
    left: 20%;
  }
  .nav__button::after {
    top: 19px;
    box-shadow: 0 6px 0 0 #f9fbfd;
  }
}

.nav__menu.shown {
  display: flex;
  max-height: 100vh;
  overflow-y: scroll;
  gap: 0;
  padding-top: 62px;
}
.nav__menu.shown li {
  padding-bottom: 26px;
}
.nav__menu.shown::before {
  content: "";
  width: 42px;
  height: 22px;
  background: url("../img/svg/logo-burger.svg");
  transform: translate(-10px, -42px);
  flex-shrink: 0;
}

.nav__menu.shown + .nav__button {
  z-index: 100;
  position: absolute;
  right: 0;
  top: 12px;
  cursor: pointer;
}
.nav__menu.shown + .nav__button::after, .nav__menu.shown + .nav__button::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 18px;
  background-color: #011c40;
  top: 45%;
  left: 25%;
  transform: rotate(45deg);
}
.nav__menu.shown + .nav__button::after {
  transform: rotate(-45deg);
  box-shadow: none;
}

.nav__menu.shown ~ .nav__logo {
  display: none;
}

.nav__overlay {
  display: none;
}

.nav__menu.shown ~ .nav__overlay {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 35px;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(0, 43, 102, 0.5);
}

.site-list {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(4, max-content);
  gap: 33px;
  row-gap: 12px;
}
@media (max-width: 1023px) {
  .site-list {
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .site-list--nav {
    background-color: #f9fbfd;
    flex-direction: column;
    height: 100vh;
    width: calc(100% - 35px);
    position: absolute;
    z-index: 100;
    top: 0;
    right: 0;
    padding: 36px 26px 15px;
    gap: 26px;
  }
  .site-list--nav .site-list__item {
    max-width: none;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .site-list--footer {
    gap: 8px;
  }
}

.site-list__item {
  max-width: 170px;
}
@media (max-width: 1023px) {
  .site-list__item {
    max-width: 150px;
  }
}

.site-list__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .site-list__link--nav {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .site-list__link--nav {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
  .site-list__link--nav:focus, .site-list__link--nav:hover {
    opacity: 0.7;
    color: #145492;
    border-bottom: 1px solid #145492;
  }
  .site-list__link--nav:active {
    opacity: 1;
  }
}
@media (max-width: 1023px) {
  .site-list__link--footer {
    font-size: 12px;
    line-height: 15px;
  }
}
.site-list__link:focus, .site-list__link:hover {
  opacity: 0.7;
  color: #d8d8d9;
  border-bottom: 1px solid #d8d8d9;
}
.site-list__link:active {
  opacity: 1;
}

.footer {
  width: 100%;
  background-color: #011c40;
  min-height: 138px;
}

.footer__container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 50px 45px;
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .footer__container {
    padding: 45px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    padding: 40px 15px;
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__logo {
  margin-right: 40px;
}
@media (max-width: 1023px) {
  .footer__logo {
    margin-right: 25px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    margin: 0;
    margin-bottom: 22px;
  }
}

.footer__menu {
  margin: 0;
  margin-right: auto;
  padding: 0;
}
@media (max-width: 767px) {
  .footer__menu {
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-bottom: 21px;
  }
}

.footer__social {
  margin: 0;
  padding: 0;
  transform: translate(-1px, 5px);
}
@media (max-width: 1023px) {
  .footer__social {
    transform: translate(-3px, 2px);
  }
}
@media (max-width: 767px) {
  .footer__social {
    transform: none;
  }
}

.social {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, max-content);
  gap: 27px;
  row-gap: 12px;
}
@media (max-width: 1023px) {
  .social {
    column-gap: 22px;
  }
}

.social__link {
  color: #f9fbfd;
}
.social__link:focus, .social__link:hover {
  color: #d8d8d9;
  opacity: 0.7;
}
.social__link:active {
  opacity: 1;
}

@media (max-width: 1023px) {
  .social__icon--ok {
    width: 10px;
    height: 16px;
  }
  .social__icon--dzen {
    width: 16px;
    height: 16px;
  }
  .social__icon--reddit {
    width: 16px;
    height: 16px;
  }
}

.hero {
  width: 100%;
  background-color: #145492;
}

.hero__container {
  position: relative;
  max-width: 100%;
  height: max-content;
  margin: 0 auto;
  display: grid;
  grid-template-columns: auto 1000px auto;
}
@media (max-width: 1023px) {
  .hero__container {
    grid-template-columns: auto 100% auto;
  }
}

.hero__image {
  grid-column: 1/-1;
  grid-row: 1/2;
  position: absolute;
  width: 100%;
  height: 100%;
}
.hero__image img {
  object-fit: cover;
  object-position: 50% 100%;
  width: 100%;
  height: 100%;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .hero__image img {
    box-shadow: none;
  }
}

.hero__description {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  padding: 270px 15px 277px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .hero__description {
    padding: 268px 46px;
    padding-bottom: 274px;
  }
}
@media (max-width: 767px) {
  .hero__description {
    padding: 175px 15px;
    padding-bottom: 179px;
  }
}
.hero__description h1 {
  padding: 0;
  margin: 0;
  margin-bottom: 13px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .hero__description h1 {
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .hero__description h1 {
    font-size: 36px;
    line-height: 45px;
    margin-bottom: 12px;
  }
}
.hero__description p {
  padding: 0;
  margin: 0;
  font-size: 20px;
  line-height: 24px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .hero__description p {
    font-size: 18px;
    line-height: 22px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .hero__description p {
    font-size: 16px;
    line-height: 22px;
    max-width: 200px;
  }
}

.promo__container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 120px 45px;
  padding-bottom: 145px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 40px;
}
@media (max-width: 1023px) {
  .promo__container {
    padding: 100px 46px;
    padding-bottom: 85px;
    column-gap: 32px;
  }
}
@media (max-width: 767px) {
  .promo__container {
    padding: 65px 15px;
    padding-bottom: 85px;
    grid-template-columns: 1fr;
  }
}

@media (max-width: 1023px) {
  .promo__description {
    max-width: 324px;
    transform: translateY(-9px);
  }
}
.promo__description h2 {
  margin: 0;
  margin-bottom: 23px;
  padding: 0;
  padding-top: 56px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023px) {
  .promo__description h2 {
    font-size: 26px;
    line-height: 36px;
    padding: 0;
    margin-bottom: 15px;
    max-width: 302px;
  }
}
@media (max-width: 767px) {
  .promo__description h2 {
    font-size: 24px;
    line-height: 34px;
    max-width: 275px;
  }
}
.promo__description p {
  margin: 0;
  padding: 0;
  max-width: 512px;
}
@media (max-width: 1023px) {
  .promo__description p {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (max-width: 767px) {
  .promo__description p {
    max-width: none;
    margin-bottom: 35px;
  }
}

@media (max-width: 1023px) {
  .promo__image {
    min-width: 324px;
    min-height: 174px;
  }
}
@media (max-width: 767px) {
  .promo__image {
    min-width: 290px;
    min-height: 156px;
  }
}

.advantages {
  width: 100%;
  background-color: #dce8f4;
}

.advantages__container {
  width: 100%;
  height: 100%;
  position: relative;
}

.advantages__image {
  position: absolute;
  width: 100%;
  height: 100%;
}
.advantages__image img {
  object-fit: cover;
  object-position: 50% 100%;
  width: 100%;
  height: 100%;
}

.advantages__content {
  max-width: 1250px;
  margin: 0 auto;
  padding-bottom: 120px;
  position: relative;
  z-index: 2;
}
@media (max-width: 1023px) {
  .advantages__content {
    padding-bottom: 101px;
  }
}
@media (max-width: 767px) {
  .advantages__content {
    padding-bottom: 64px;
  }
}
.advantages__content h2 {
  width: max-content;
  margin: 0 auto;
  margin-bottom: 49px;
  padding-top: 110px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023px) {
  .advantages__content h2 {
    padding-top: 89px;
