.footer {
  width: 100%;
  background-color: $tangaroa;
  min-height: 138px;
}

.footer__container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 50px 45px;

  display: flex;
  align-items: center;

  @media (max-width: $tablet-width) {
    padding: 45px;
  }

  @media (max-width: $mobile-width) {
    padding: 40px 15px;
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__logo {
  margin-right: 40px;

  @media (max-width: $tablet-width) {
    margin-right: 25px;
  }

  @media (max-width: $mobile-width) {
    margin: 0;
    margin-bottom: 22px;
  }
}

.footer__menu {
  margin: 0;
  margin-right: auto;
  padding: 0;

  @media (max-width: $mobile-width) {
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-bottom: 21px;
  }
}

.footer__social {
  margin: 0;
  padding: 0;
  transform: translate(-1px, 5px);

  @media (max-width: $tablet-width) {
    transform: translate(-3px, 2px);
  }

  @media (max-width: $mobile-width) {
    transform: none;
  }
}
