.hero {
  width: 100%;
  background-color: $endeavour;
}

.hero__container {
  position: relative;
  max-width: 100%;
  height: max-content;
  margin: 0 auto;
  display: grid;
  grid-template-columns: auto 1000px auto;

  @media (max-width: $tablet-width) {
    grid-template-columns: auto 100% auto;
  }
}

.hero__image {
  grid-column: 1 / -1;
  grid-row: 1 / 2;
  position: absolute;
  width: 100%;
  height: 100%;


  img {
    object-fit: cover;
    object-position: 50% 100%;
    width: 100%;
    height: 100%;
    box-shadow: (0 4px 4px rgba($color-default-black, 0.25));

    @media (max-width: $tablet-width) {
      box-shadow: none;
    }
  }
}

.hero__description {
  position: relative;
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  padding: 270px 15px 277px;
  color: $alice-blue;

  @media (max-width: $tablet-width) {
    padding: 268px 46px;
    padding-bottom: 274px;
  }

  @media (max-width: $mobile-width) {
    padding: 175px 15px;
    padding-bottom: 179px;
  }

  h1 {
    padding: 0;
    margin: 0;
    margin-bottom: 13px;

    font-weight: 700;
    font-size: 54px;
    line-height: 66px;
    text-shadow: 0 4px 4px rgba($color-default-black, 0.25);

    @media (max-width: $tablet-width) {
      font-size: 50px;
      line-height: 61px;
      text-shadow: none;
    }

    @media (max-width: $mobile-width) {
      font-size: 36px;
      line-height: 45px;
      margin-bottom: 12px;
    }
  }

  p {
    padding: 0;
    margin: 0;

    font-size: 20px;
    line-height: 24px;
    text-shadow: 0 4px 4px rgba($color-default-black, 0.25);

    @media (max-width: $tablet-width) {
      font-size: 18px;
      line-height: 22px;
      text-shadow: none;
    }

    @media (max-width: $mobile-width) {
      font-size: 16px;
      line-height: 22px;
      max-width: 200px;
    }
  }
}
